

.jugador{
    display: flex;
    justify-content: space-between;
    flex-direction: row;
    justify-content: space-between;
    font-size: 8rem;
    font-family:  Arial;
    margin-bottom: 40px;

}
    
.pelota{
    width: 30px;
    width: 95px;
}
.nombre{
    width: 66%;
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    margin-right: 124px;
}
.nombre_jugador{
    font-weight: bold;
    width: 80%;
    font-family: arial;
    color:#e5ec0a;
}
.contenedor{width: 100%;
    padding: 0px 60px;
    padding-top: 5%;
    color: black;}
.iconoSaque_hidden{
        visibility: hidden;
}
.set{
    color:#ffffff;
    font-weight: bold;
    display: flex;
    flex-direction: column-reverse;
    margin-top: -153px;
    width: 140px;
    text-align: center;
}
.set2{
    font-family:  Arial;
    color: #e5ec0a;
    font-size: 58px;

}
body{background-color:#114d0b !important;
overflow:hidden !important;
cursor:none !important;}
.logo{
    width: 401px;
}
.divLogo{
    align-self: flex-end;
}
.pista{
    width: 100%;
    color:#ffffff;
    font-size: 60px;
    margin-top: 50px;
    font-family:  Arial;
    align-self: center;
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    padding: 0px 60px;
}
.tiempo{
    color: white;
    /* text-align: end; */
    font-size: 60px;
    align-self: flex-end;
    /* padding-right: 60px; */
    font-family: fantasy;
}
.paddingFix{margin-bottom: 40px;}
.puntos{
    font-family: Arial;
   
    font-weight: bold;
    color: white;
}
.logos{
    display: flex;
    flex-direction: row;
    justify-content: space-around;
    padding: 50px 65px;
    padding-top:0px;
}
@media (max-width: 1780px){
    .jugador{font-size: 7rem;}
    .set2{font-size: 3rem;}
}
@media (max-width: 1611px){
    .jugador{font-size: 6rem;}
    .set2{font-size: 3rem;}
    .logo{width: 360px;}
    .tiempo{font-size: 4rem;}
}
@media (max-width: 1458px){
    .jugador{font-size: 5rem;}
    .set2{font-size: 3rem;}
}
@media (max-width: 1538px){
    .logo{width: 300px;}
    .tiempo{font-size: 3.5rem;}
}
@media (max-width: 1300px){
    .jugador{font-size: 4rem;}
    .set2{font-size: 2.5rem;}
    .logo{width: 250px;}
    .tiempo{font-size: 3rem;}
}
@media (max-width: 1154px){
    .jugador{font-size: 3rem;}
    .set2{font-size: 2.5rem;}
    .pista{font-size: 3rem;}
    .logo{width: 180px;}
    
}
@media (max-width: 768px){
    .jugador{font-size: 2rem;}
    .set2{font-size: 1.5rem;}
    .pista{font-size: 2rem;margin: 20px 0px;}
    .logo{width: 180px;}
    .pelota{width: 40px;}
    .nombre{margin-right: 45px;}
    .set{width: 100px;}
    .logo {width: 100px;}
    .tiempo {font-size: 2rem;}
}
@media (max-width: 690px){
    .set {width: 90px;}
}
@media (max-width: 670px){
    .jugador {font-size: 1.5rem;}
    .pista{font-size: 1.5rem;}
    .tiempo {font-size: 1.5rem;}
}